.App {
  text-align: center;
}
.router{
  background-color: #61dafb;
}

body{
  margin: 0;
}

.Navigator{
  list-style-type: none;
  margin: 0;
  padding: 0;
  width: 100px;
  background-color: #f1f1f1;
  position: fixed;
  height: 100%;
  overflow: auto;
}

.body{
  float: left;
  margin-left: 100px;
}

.body > div {
  padding: 10px;
}

/* .Navigator > li {
  display: ;
} */

.black-nav{
  background-color: black;
  width: 100%;
  display: flex;
  color: white;
  padding: 20px;
  font-weight: 600;
  font-size: 20px;
  margin-left: 100px;
}

.list{
  text-align: left;
}

.modal{
  maring-top: 20px;
  padding: 20px;
  background: #eee;
}






.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
